<template>
  <div class="container">
    <el-card class="box-card">
      <div class="search">
        <el-form>
          <el-form-item label="关键字" label-width="80px">
            <el-input
              v-model="query.keyword"
              placeholder="根据编号搜索"
            ></el-input>
          </el-form-item>
        </el-form>
        <div class="button">
          <el-button @click="query.keyword = ''">清除</el-button>
          <el-button type="primary" @click="search">搜索</el-button>
        </div>
      </div>
      <el-alert
        :title="'数据一共' + total + '条'"
        type="info"
        show-icon
        :closable="false"
      >
      </el-alert>
      <el-table :data="questionsList" stripe style="width: 100%">
        <el-table-column prop="id" label="编号" width="220"> </el-table-column>
        <el-table-column label="题型" width="80">
          <template slot-scope="scope">
            {{
              scope.row.questionType == 1
                ? '单选'
                : scope.row.questionType == 2
                ? '多选'
                : '简答'
            }}
          </template>
        </el-table-column>
        <el-table-column label="题目编号" width="220">
          <template slot-scope="scope">
            <p
              v-for="(item, index) in scope.row.questionIDs"
              :key="index"
              style="color:#0e83fe;cursor:pointer;margin:0;padding:0"
              @click="preview(item.id)"
            >
              {{ item.number }}
            </p>
          </template>
        </el-table-column>
        <el-table-column prop="addTime" label="录入时间" width="180">
        </el-table-column>
        <el-table-column prop="totalSeconds" label="答题时间(s)">
        </el-table-column>
        <el-table-column prop="accuracyRate" label="正确率(%)">
        </el-table-column>
        <el-table-column prop="userName" label="录入人"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              type="danger"
              icon="el-icon-delete"
              circle
              plain
              size="mini"
              @click="deleteQuestion(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="block">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="this.query.page"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="this.query.pagesize"
          layout="prev, pager, next,  sizes,total, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!-- 题目预览对话框 -->
    <el-dialog
      title="题目预览"
      :visible.sync="dialogVisible"
      width="900px"
      :before-close="handleClose"
    >
      <questions-preview
        :dialogData="dialogData"
        v-model="videos"
      ></questions-preview>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"
          >关 闭</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { detail, randoms, removeRandoms } from '@/api/hmmm/questions.js'
import questionsPreview from '@/module-hmmm/components/questions-preview'
export default {
  // 注册题目预览组件
  components: {
    questionsPreview
  },
  data () {
    return {
      // 定义题库列表数组
      questionsList: [],
      // 题库总条数
      total: 0,
      // 获取题库列表传递的参数
      query: {
        keyword: '',
        page: 1,
        pagesize: 5
      },
      // dialogVisible
      dialogVisible: false,
      // 预览对话框数据
      dialogData: [],
      videos: false
    }
  },
  created () {
    this.getQuestionsList()
  },
  methods: {
    // 题目预览对话框
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    // 获取题库列表
    async getQuestionsList () {
      const res = await randoms(this.query)
      console.log(res, 778899)
      this.questionsList = res.data.items
      console.log(this.questionsList)
      this.total = res.data.counts
    },
    // 组题列表删除
    deleteQuestion (data) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          console.log(data.id, 1111)
          const res = await removeRandoms({ data })
          console.log(res, 888999)
          // 刷新页面
          this.getQuestionsList()
          if (res.status === 200) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 关键词搜索
    search () {
      this.getQuestionsList()
    },
    // 分页，换每页的条数
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.query.pagesize = val
      this.getQuestionsList()
    },
    // 分页，换当前页
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.query.page = val
      this.getQuestionsList()
    },
    // 题目预览
    async preview (id) {
      console.log(id)
      this.dialogVisible = true
      const res = await detail({
        id
      })
      console.log(res, 888999)
      this.dialogData = res.data
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.box-card {
  margin: 15px 10px;
  .search {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    height: 40px;
    .el-form {
      width: 400px;
      .el-input__inner {
        width: 300px;
      }
    }
  }

  .el-table {
    margin-top: 20px;
  }
  .el-pagination {
    margin-top: 20px;
    text-align: center;
  }
}
::v-deep .el-dialog__footer {
  text-align: right;
}
</style>
